import Vue from 'vue'
import Router from 'vue-router'
import Pay from '@/components/Pay'
import Home from '@/components/Home'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Pay',
      component: Pay
    },
    {
      path: '/Home',
      name: 'Home',
      component: Home
    }
  ]
})



