
import VueAliplayer from 'vue-aliplayer'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Panel from 'vux/src/components/panel/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'


export default {
components: {
Flexbox,
FlexboxItem,
XButton,
Group,
XTextarea,
Panel,
XHeader,
Cell,
'ali-player': VueAliplayer
},
data () {
return {
num: '666',
msg: '',
title: 'title',
lecturer: 'lecturer',
time: 'time',
list: [{
src: 'http://placeholder.qiniudn.com/60x60/3cc51f/ffffff',
title: ' 16:42',
desc: ''
}, {
src: 'http://placeholder.qiniudn.com/60x60/3cc51f/ffffff',
title: ' 16:42',
desc: ''
}],
aplayer: {
height: '4.2rem',
X5_fullscreen: 'true',
x5_type: 'h5',
source: 'http://static.smartisanos.cn/common/video/t1-ui.mp4',
vid: '8db6e5c7ff5f4257b41e2487ec61d592',
playauth:
''
},
player: null
}
},
methods: {
sent () {
let newMsg = {
src: 'http://placeholder.qiniudn.com/60x60/3cc51f/ffffff',
title: '' + this.num,
desc: this.msg
}
this.list.push(newMsg)
this.msg = ''
this.$nextTick(() => {
this.$refs.chatHeight.scrollTop = this.$refs.chatHeight.children[0].offsetHeight - this.$refs.sentBoxHeight.offsetHeight
})
},
played () {
console.log('play callback')
}
}
}



